<template>
  <main class="mdl-layout__content mdl-color--indigo page">
    <div class="mdl-grid">
      <div class="mdl-cell mdl-cell--12-col mdl-grid mdl-grid--no-spacing">
          <form method="post" class="login mdl-color-text--white" novalidate @submit.prevent="proceedLogin" @keydown.enter="proceedLogin" autocomplete="off">
            <h2 class="">Login</h2>
            <div>
              <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'is-invalid is-dirty': errors.has('username')}">
                  <input v-validate="'required|email'" name="username" class="mdl-textfield__input" type="email" id="user" autocomplete="off" v-model="formParams.username">
                  <label class="mdl-textfield__label" for="user">Username</label>
                  <span class="mdl-textfield__error">{{ errors.first('username') }}</span>
                </div>
            </div>
            <div>
                <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label"  :class="{'is-invalid is-dirty': errors.has('password')}">
                  <input v-validate="{ required: true, min: 6 }" name="password" class="mdl-textfield__input" type="password" id="password" autocomplete="off" v-model="formParams.password">
                  <label class="mdl-textfield__label" for="pasword">Password</label>
                  <span class="mdl-textfield__error">{{ errors.first('password') }}</span>
                </div>
            </div>
              <button class="mdl-button mdl-js-button mdl-button--raised mdl-js-ripple-effect mdl-color--white">
                Login
              </button>
              <div>
                <a class="mdl-color-text--white" href="#">Forgot Password?</a>
              </div>
          </form>
        </div>
    </div>
  </main>
</template>

<script>
import { Auth } from 'MIXINS/auth'
import { mapActions } from 'vuex'
// import { setTimeout } from 'timers';

export default {
  name: 'Login',
  mixins: [Auth],
  beforeMount () {
    this.redirectToDashboard()
  },
  methods: {
    ...mapActions([
      'userLogin',
      'fetchOrganizations',
      'fetchRawMaterials'
    ]),
    fetchData () {
      this.fetchOrganizations().then(() => {
      //   return this.fetchRawMaterials()
      // }).then(() => {
        this.redirectToDashboard()
      })
    },
    proceedLogin () {
      this.$validator.validateAll().then((result) => {
        if (!result) {
          console.log('Cannot validate form', result)
        } else {
          this.userLogin(this.formParams).then(() => {
            return this.fetchData()
          }).catch(e => {
            this.$toastr.e(e)
          })
        }
      })
    },
    redirectToDashboard () {
      if (this.authenticated) {
        this.$router.replace({ name: 'user.dashboardView' })
        // setTimeout(() => , 1000)
      }
    }
  },
  data () {
    return {
      formParams: {
        username: '',
        password: ''
      }
    }
  }
}
</script>



